import React,{useState ,useReducer,useContext,
    useLayoutEffect,useEffect ,useRef, memo,useMemo} from 'react'

import MyContext from '../../lib/my-context'

function countReducer(state,action){
    switch (action.type) {
        case 'add':
            return state+1
            
        case 'minus':
            return state-1
    
        default:
            return state;
    }
}

function MyCountFunc(){
    const [count,dispatchCount]= useReducer(countReducer,0)
    const [name,setName] = useState('charlie')
// 每一次 MyCountFunc 组件 刷新，该config 都会重新生成新的对象，
    const config = useMemo(()=>({
        text:`count is ${count}`,
        color:count>3? 'red':'blue',
    }),[count])

    // 每一次 MyCountFunc 组件 刷新，()=>dispatchCount({type:'add'}) 都会重新生成新的匿名函数，
    return (
        <div>
            <input  value={name} onChange={(e)=>setName(e.target.value)}/>
            <Child
              config={config}
              onButtonClick={ ()=>dispatchCount({type:'add'})}
            />
        </div>
    )
}

const Child = memo( function Child({onButtonClick,config}) {
    console.log('child render')
    return (
        <button onClick={onButtonClick} style={{ color:config.color}}>
        {config.text}
        </button>
    )
})
export default MyCountFunc